import React, { useState, useEffect, useMemo } from "react";
import { Link } from "react-router-dom";
import {
  Grid,
  Typography,
  Box,
  TextField,
  Alert,
  Card,
  CardMedia,
  CardContent,
  Button,
  InputAdornment,
  Chip,
  IconButton,
} from "@mui/material";
import SearchIcon from "@mui/icons-material/Search";
import BoltIcon from "@mui/icons-material/Bolt";
import PushPinIcon from "@mui/icons-material/PushPin";
import PushPinOutlinedIcon from "@mui/icons-material/PushPinOutlined";
import { motion } from "framer-motion";
import UserGuard from "../../../../utils/UserGuard";
import { mobileNumberFilter } from "../../../../baseUrl";

const categories = ["ALL", "DMT", "AEPS", "BBPS"];
const MAX_PINNED = 6;
const MAX_RECENT = 6;
const MAX_MOST_USED = 6;

const LS_KEYS = {
  PINNED: "bm_pinned_services",
  RECENT: "bm_recent_services",
  MOST_USED: "bm_most_used_services",
};

const SERVICE_FEATURES = {
  DMT: "Fast  Secure  RBI",
  AEPS: "Biometric  NPCI  Aadhaar",
  BBPS: "Utility  Bharat BillPay",
};

const COMPLIANCE = {
  DMT: " Encrypted   RBI",
  AEPS: " Encrypted   NPCI",
  BBPS: " Encrypted   BBPS",
};

const getTimeSlot = () => {
  const hour = new Date().getHours();
  if (hour >= 5 && hour < 12) return "morning";
  if (hour >= 12 && hour < 17) return "afternoon";
  if (hour >= 17 && hour < 21) return "evening";
  return "night";
};

const TIME_BASED_GREETINGS = {
  morning: [
    {
      title: "Good Morning",
      sub: "Start your day with secure digital payments",
    },
    {
      title: "Morning!",
      sub: "Check balances, settlements & payouts",
    },
    {
      title: "Ready for Business",
      sub: "All banking services are live and secure",
    },
  ],

  afternoon: [
    {
      title: "Good Afternoon",
      sub: "Fast, compliant & real-time transactions",
    },
    {
      title: "Midday Status",
      sub: "Monitor transfers and AEPS activity",
    },
    {
      title: "Business in Motion",
      sub: "Payments running smoothly across services",
    },
  ],

  evening: [
    {
      title: "Good Evening",
      sub: "Review todays transactions & settlements",
    },
    {
      title: "End-of-Day Check",
      sub: "Track earnings and service performance",
    },
    {
      title: "Stay in Control",
      sub: "Smart finance for a productive tomorrow",
    },
  ],

  night: [
    {
      title: "Good Night",
      sub: "Your money is protected with bank-grade security",
    },
    {
      title: "Always On",
      sub: "247 fintech services, even after hours",
    },
    {
      title: "System Active",
      sub: "Transactions will continue uninterrupted",
    },
  ],
};

const ROLE_SUBTEXT = {
  Admin: "Monitor platform health & settlements",
  Distributor: "Track commissions & agent activity",
  Retailer: "Process transactions faster today",
  User: "Quick access to daily services",
};

const getLS = (key, fallback = []) =>
  JSON.parse(localStorage.getItem(key)) || fallback;

const setLS = (key, value) => localStorage.setItem(key, JSON.stringify(value));

const BADGE_COLORS = {
  "MOST USED": {
    bg: "linear-gradient(135deg, #ff9800, #f57c00)",
    color: "#fff",
  },
  FAST: {
    bg: "linear-gradient(135deg, #2196f3, #1565c0)",
    color: "#fff",
  },
  RECOMMENDED: {
    bg: "linear-gradient(135deg, #4caf50, #2e7d32)",
    color: "#fff",
  },
  PINNED: {
    bg: "linear-gradient(135deg, #9c27b0, #6a1b9a)",
    color: "#fff",
  },
  RECENT: {
    bg: "linear-gradient(135deg, #607d8b, #455a64)",
    color: "#fff",
  },
};

const ServiceCard = ({ service, pinned, onPin, onClick, badge }) => {
  const serviceName = service?.service_name || "Unnamed Service";
  const serviceIcon = service?.icon || "/servicesicon.png";
  const pathName = service?.route || "#";

  return (
    <Grid item xs={6} sm={4} md={3} lg={2}>
      <Link to={pathName} onClick={onClick} style={{ textDecoration: "none" }}>
        <motion.div whileHover={{ y: -8, scale: 1.02 }}>
          <Card
            className="boxModel"
            sx={{
              height: "100%",
              borderRadius: "28px",
              padding: "26px 18px",
              position: "relative",
              overflow: "hidden",
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              textAlign: "center",
              background:
                "linear-gradient(145deg, rgba(255,255,255,0.9), rgba(245,247,250,0.85))",
              backdropFilter: "blur(12px)",
              border: "4px solid rgba(255, 193, 7, 0.35)",
              boxShadow:
                "0 10px 30px rgba(0,0,0,0.06), inset 0 1px 0 rgba(255,255,255,0.6)",
              transition: "all 0.45s cubic-bezier(0.4, 0, 0.2, 1)",
              "&::before": {
                content: '""',
                position: "absolute",
                inset: 0,
                backgroundImage: `url(${serviceIcon})`,
                backgroundRepeat: "no-repeat",
                backgroundPosition: "center",
                backgroundSize: "180px",
                opacity: 0.05,
                filter: "grayscale(100%)",
                transition: "all 0.45s ease",
                zIndex: 0,
              },
              "&::after": {
                content: '""',
                position: "absolute",
                inset: 0,
                background:
                  "linear-gradient(120deg, transparent 30%, rgba(255,255,255,0.8), transparent 70%)",
                opacity: 0,
                transform: "translateX(-100%)",
                transition: "all 0.6s ease",
                zIndex: 1,
              },
              "&:hover": {
                transform: "translateY(-10px) scale(1.035)",

                boxShadow:
                  "0 24px 50px rgba(0,0,0,0.15), 0 0 0 2px rgba(255,193,7,0.4)",

                "&::before": {
                  opacity: 0.1,
                  transform: "scale(1.15) rotate(8deg)",
                },

                "&::after": {
                  opacity: 1,
                  transform: "translateX(100%)",
                },

                "& .MuiCardMedia-root": {
                  transform: "scale(1.18)",
                  filter: "drop-shadow(0 10px 20px rgba(0,0,0,0.2))",
                },

                "& .service-title": {
                  fontWeight: 700,
                  letterSpacing: "0.4px",
                  color: "#111",
                },
              },
            }}
          >
            <IconButton
              onClick={(e) => {
                e.preventDefault();
                e.stopPropagation();
                onPin();
              }}
              sx={{
                position: "absolute",
                top: 10,
                right: 10,
                zIndex: 3,
                bgcolor: "#fff",
                boxShadow: 1,
              }}
            >
              {pinned ? <PushPinIcon /> : <PushPinOutlinedIcon />}
            </IconButton>

            {/*  BADGE */}
            {badge && (
              <Chip
                label={badge}
                size="small"
                sx={{
                  position: "absolute",
                  top: 10,
                  left: 10,
                  zIndex: 3,

                  background: BADGE_COLORS[badge]?.bg || "#e0e0e0",
                  color: BADGE_COLORS[badge]?.color || "#000",
                  fontSize: 10,
                  fontWeight: 700,
                }}
              />
            )}

            {/*  ICON */}
            <CardMedia
              component="img"
              src={serviceIcon}
              alt={serviceName}
              loading="eager"
              onError={(e) => {
                e.target.onerror = null;
                e.target.src = "/servicesicon.png";
              }}
              sx={{
                width: { xs: 40, sm: 50 },
                height: { xs: 40, sm: 50 },
                objectFit: "contain",
                marginBottom: 2,
                position: "relative",
                zIndex: 2,
                filter: "drop-shadow(0 2px 4px rgba(0,0,0,0.08))",
                transition: "all 0.4s ease",
              }}
            />

            {/*  TITLE */}
            <CardContent
              sx={{
                padding: 0,
                position: "relative",
                zIndex: 2,
                width: "100%",
              }}
            >
              {/*  TITLE */}
              <Typography
                className="service-title"
                variant="body2"
                sx={{
                  fontSize: { xs: "13px", sm: "14px" },
                  fontWeight: 700,
                  lineHeight: 1.3,
                  color: "#2c3e50",
                  mb: 0.5,
                  WebkitLineClamp: 2,
                  WebkitBoxOrient: "vertical",
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                }}
              >
                {serviceName}
              </Typography>

              {/*  SERVICE FEATURES */}
              {SERVICE_FEATURES?.[service?.service_category] && (
                <Typography
                  sx={{
                    fontSize: "11px",
                    fontWeight: 500,
                    color: "#6c757d",
                    mb: 0.5,
                  }}
                >
                  {SERVICE_FEATURES[service.service_category]}
                </Typography>
              )}

              {/*  COMPLIANCE */}
              {COMPLIANCE?.[service?.service_category] && (
                <Typography
                  sx={{
                    fontSize: "10.5px",
                    fontWeight: 600,
                    color: "#198754",
                    lineHeight: 1.3,
                  }}
                >
                  {COMPLIANCE[service.service_category]}
                </Typography>
              )}
            </CardContent>
          </Card>
        </motion.div>
      </Link>
    </Grid>
  );
};

function ServiceGrid({ services = [] }) {
  const userNumber = localStorage.getItem("regisNumber");
  const storedUserType = localStorage.getItem("userType");

  const [searchTerm, setSearchTerm] = useState("");
  const [activeCategory, setActiveCategory] = useState("ALL");

  const [pinned, setPinned] = useState([]);
  const [recent, setRecent] = useState([]);
  const [mostUsed, setMostUsed] = useState([]);

  const [searchHintIndex, setSearchHintIndex] = useState(0);
  const [greetingIndex, setGreetingIndex] = useState(0);

  const SEARCH_HINTS = useMemo(() => {
    if (!services?.length) return [];

    const hints = new Set();

    services.forEach((s) => {
      if (!s?.service_name) return;

      hints.add(s.service_name);

      if (s.service_name.includes("Transfer")) {
        hints.add("Bank Transfer");
        hints.add("Money Transfer");
      }
      if (s.service_name.includes("Recharge")) {
        hints.add("Mobile Recharge");
        hints.add("Recharge");
      }
      if (s.service_name.includes("Electricity")) {
        hints.add("Electricity Bill");
      }
      if (s.service_name.includes("Aadhaar")) {
        hints.add("AEPS Aadhaar");
        hints.add("Aadhaar Services");
      }
      if (s.service_name.includes("Gas")) {
        hints.add("Gas Bill");
      }
      if (s.service_name.includes("Insurance")) {
        hints.add("Insurance Payment");
      }
    });

    return Array.from(hints);
  }, [services]);

  /* INIT LS */
  useEffect(() => {
    setPinned(getLS(LS_KEYS.PINNED));
    setRecent(getLS(LS_KEYS.RECENT));
    setMostUsed(getLS(LS_KEYS.MOST_USED));
  }, []);

  /*  SEARCH HINT ROTATION */
  useEffect(() => {
    if (!SEARCH_HINTS.length) return;

    const interval = setInterval(() => {
      setSearchHintIndex((p) => (p + 1) % SEARCH_HINTS.length);
    }, 3000);

    return () => clearInterval(interval);
  }, [SEARCH_HINTS]);

  /*  GREETING ROTATION */

  const timeSlot = getTimeSlot();
  const greetings = TIME_BASED_GREETINGS[timeSlot];

  useEffect(() => {
    const interval = setInterval(() => {
      setGreetingIndex((prev) => (prev + 1) % greetings.length);
    }, 5000);

    return () => clearInterval(interval);
  }, [timeSlot]);

  const handlePin = (service) => {
    const updated = pinned.some((s) => s._id === service._id)
      ? pinned.filter((s) => s._id !== service._id)
      : [service, ...pinned].slice(0, MAX_PINNED);

    setPinned(updated);
    setLS(LS_KEYS.PINNED, updated);
  };

  const handleClick = (service) => {
    const r = [service, ...recent.filter((s) => s._id !== service._id)].slice(
      0,
      MAX_RECENT,
    );
    setRecent(r);
    setLS(LS_KEYS.RECENT, r);

    const usage = getLS(LS_KEYS.MOST_USED, {});
    usage[service._id] = (usage[service._id] || 0) + 1;
    setLS(LS_KEYS.MOST_USED, usage);

    const sorted = Object.entries(usage)
      .sort((a, b) => b[1] - a[1])
      .map(([id]) => services.find((s) => s._id === id))
      .filter(Boolean)
      .slice(0, MAX_MOST_USED);

    setMostUsed(sorted);
  };

  const filtered = useMemo(() => {
    return services.filter(
      (s) =>
        s.service_name?.toLowerCase().includes(searchTerm.toLowerCase()) &&
        (activeCategory === "ALL" || s.service_category === activeCategory),
    );
  }, [services, searchTerm, activeCategory]);

  const grouped = useMemo(() => {
    return filtered.reduce((acc, s) => {
      acc[s.service_category] = acc[s.service_category] || [];
      acc[s.service_category].push(s);
      return acc;
    }, {});
  }, [filtered]);

  return (
    <Box sx={{ py: 4, bgcolor: "#f8f9fb" }}>
      <UserGuard
        currentUser={storedUserType}
        excludeUserList={["Reseller", "reseller partner api user"]}
      >
        {mobileNumberFilter?.includes(userNumber) !== true && (
          <Box
            sx={{
              mb: 4,
              borderRadius: 4,
              bgcolor: "#ffffff",
              textAlign: "center",
            }}
          >
            <Typography variant="h4" fontWeight={900}>
              {greetings[greetingIndex].title}
            </Typography>
            <Typography sx={{ opacity: 0.85, mt: 1 }}>
              {ROLE_SUBTEXT[storedUserType] || greetings[greetingIndex].sub}
            </Typography>
          </Box>
        )}
      </UserGuard>

      {/*  SEARCH */}
      <TextField
        fullWidth
        placeholder={`Search ${SEARCH_HINTS[searchHintIndex]}...`}
        value={searchTerm}
        onChange={(e) => setSearchTerm(e.target.value)}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
              <SearchIcon />
            </InputAdornment>
          ),
        }}
        sx={{ mb: 4, bgcolor: "#fff", borderRadius: "999px" }}
      />

      {/* CATEGORY FILTER */}
      <Box sx={{ display: "flex", gap: 2, mb: 4 }}>
        {categories.map((c) => (
          <Button
            key={c}
            variant={activeCategory === c ? "contained" : "outlined"}
            onClick={() => setActiveCategory(c)}
          >
            {c}
          </Button>
        ))}
      </Box>

      {/* PINNED */}
      {pinned.length > 0 && (
        <>
          <Typography fontWeight={900} mb={2}>
            PINNED
          </Typography>
          <Grid container spacing={3}>
            {pinned.map((s) => (
              <ServiceCard
                key={s._id}
                service={s}
                pinned
                badge="PINNED"
                onPin={() => handlePin(s)}
                onClick={() => handleClick(s)}
              />
            ))}
          </Grid>
        </>
      )}

      {/* MOST USED */}
      {mostUsed.length > 0 && (
        <>
          <Typography fontWeight={900} mt={5} mb={2}>
            MOST USED
          </Typography>
          <Grid container spacing={3}>
            {mostUsed.map((s) => (
              <ServiceCard
                key={s._id}
                service={s}
                pinned={pinned.some((p) => p._id === s._id)}
                badge="MOST USED"
                onPin={() => handlePin(s)}
                onClick={() => handleClick(s)}
              />
            ))}
          </Grid>
        </>
      )}

      {/* CATEGORY SERVICES */}
      {Object.entries(grouped).map(([cat, list]) => (
        <Box key={cat} mt={5}>
          <Typography fontWeight={900} mb={2}>
            {cat}
          </Typography>
          <Grid container spacing={3}>
            {list.map((s, i) => (
              <ServiceCard
                key={s._id}
                service={s}
                pinned={pinned.some((p) => p._id === s._id)}
                badge={
                  [
                    "B2B UPI",
                    "B2B Bank Transfer",
                    "Bank Transfer",
                    "PPI Bank Transfer",
                    "Electricity",
                    "Electricity (Prepaid)",
                    "Mobile Recharge",
                    "Fastag",
                    "Credit Card",
                  ].includes(s?.service_name)
                    ? "MOST USED"
                    : [
                          "AFT",
                          "B2B UPI",
                          "Metro Recharge",
                          "NCMC Recharge",
                          "DTH",
                          "Mobile Postpaid",
                          "IRCTC",
                        ].includes(s?.service_name)
                      ? "FAST"
                      : ["AEPS"].includes(s?.service_category)
                        ? "MOST USED"
                        : null
                }
                onPin={() => handlePin(s)}
                onClick={() => handleClick(s)}
              />
            ))}
          </Grid>
        </Box>
      ))}

      {recent.length > 0 && (
        <Box mt={6}>
          <Typography fontWeight={900} mb={2}>
            RECENTLY USED
          </Typography>
          <Grid container spacing={3}>
            {recent.map((s) => (
              <ServiceCard
                key={s._id}
                service={s}
                pinned={pinned.some((p) => p._id === s._id)}
                badge="RECENT"
                onPin={() => handlePin(s)}
                onClick={() => handleClick(s)}
              />
            ))}
          </Grid>
        </Box>
      )}

      {filtered.length === 0 && (
        <Alert severity="info">No services found</Alert>
      )}
    </Box>
  );
}

export default ServiceGrid;